import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductsAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((product) => product.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
  },
  getters: {
    cartDetailProducts(state) {
      // eslint-disable-next-line arrow-body-style
      return state.cartProducts.map((item) => {
        const { product } = state.cartProductsData.find((p) => p.product.id === item.productId);

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      // eslint-disable-next-line max-len
      return getters.cartDetailProducts.reduce((acc, item) => item.product.price * item.amount + acc, 0);
    },
    getTotalAmount(state, getters) {
      // eslint-disable-next-line max-len
      return getters.cartDetailProducts.reduce((total, item) => total + item.amount, 0);
    },
  },
  actions: {
    async loadCart(context) {
      return axios
        .get(`${API_BASE_URL}api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            const { accessKey } = response.data.user;
            localStorage.setItem('accessKey', accessKey);
            context.commit('updateUserAccessKey', accessKey);
          }

          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    async addProductToCart(context, { productId, quantity }) {
      return axios
        .post(
          `${API_BASE_URL}api/baskets/products`,
          {
            productId,
            quantity,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    async updateCartProductAmount(context, { productId, quantity }) {
      context.commit(
        'updateCartProductsAmount',
        {
          productId,
          amount: quantity,
        },
      );

      if (quantity < 1) {
        return;
      }

      // eslint-disable-next-line consistent-return
      return axios
        .put(
          `${API_BASE_URL}api/baskets/products`,
          {
            productId,
            quantity,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    async deleteCartProduct(context, { productId }) {
      return axios
        .delete(
          `${API_BASE_URL}api/baskets/products`,
          {
            data: {
              productId,
            },
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    async loadOrderInfo(context, orderId) {
      return axios
        .get(`${API_BASE_URL}api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
  },
});
