<template>
  <li class="cart__item product" >
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title" />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <span class="product__code">Артикул: {{ item.productId }}</span>

    <ProductCounter
      v-model="amount"
      :min="1"
      :max="100"
      class="product__counter"
    />

    <b class="product__price"> {{ (item.amount * item.product.price) | numberFormat }} ₽ </b>

    <button class="product__del button-del"
            type="button"
            aria-label="Удалить товар из корзины"
            @click.prevent="deleteProduct()">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="img/svg/sprite.svg#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import ProductCounter from '@/components/products/ProductCounter.vue';

export default {
  components: {
    ProductCounter,
  },
  filters: {
    numberFormat,
  },
  props: [
    'item',
  ],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.updateCartProductAmount({
          productId: this.item.productId,
          quantity: value,
        });
      },
    },
  },
  methods: {
    ...mapActions([
      'deleteCartProduct',
      'updateCartProductAmount',
    ]),

    deleteProduct() {
      this.deleteCartProduct({
        productId: this.item.productId,
      });
    },
  },
};
</script>
