<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="decrement">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="img/svg/sprite.svg#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="amount" />

    <button type="button" aria-label="Добавить один товар" @click.prevent="increment">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="img/svg/sprite.svg#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1,
    },
    min: {
      type: Number,
      default: 1,
    },
    max: {
      type: Number,
      default: 100,
    },
  },
  data() {
    return {
      amount: this.value,
    };
  },
  methods: {
    decrement() {
      if (this.amount > this.min) {
        this.amount -= 1;
        this.$emit('input', this.amount);
      }
    },
    increment() {
      if (this.amount < this.max) {
        this.amount += 1;
        this.$emit('input', this.amount);
      }
    },
  },
};
</script>
